/**
 * 
 */
class StateElement {
    constructor(machine, state, callback) {
        this._machine = machine;
        this._state = state;
        this.callback = callback;
    }
    get machine() {
        return this._machine;
    }
    get state() {
        return this._state;
    }
    execute(iteration) {
        if (this.callback) {
            return this.callback(this, iteration);
        }
    }
}
/**
 * 
 */
class StateMachineCollection {
    constructor(machine) {
        this.machine = machine;
        this._elements = [];
    }
    get elements() {
        return this._elements;
    }
    add(el) {
        var self = this;
        if (el) {
            var toUpdate = [];
            if (!Array.isArray(el)) {
                toUpdate = [el];
            } else {
                toUpdate = el;
            }
            toUpdate.forEach((tu)=>self.elements.push(tu));
        }
        return this;
    }
    clear() {
        this._elements = [];
    }
}

/**
 * 
 */
class EntityElement extends StateElement {
    constructor(machine, state, callback) {
        super(machine, state, callback);
        this.state.children = [];
    }
    get x() {
        return this.state.x;
    }
    get y() {
        return this.state.y;
    }
    get parent() {
        return this.state.parent;
    }
    set parent(p) {
        this.state.parent = p;
    }
    get children() {
        return this.state.children;
    }
}

/**
 * 
 */
class StateRenderer extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            return this.callback(el, iteration);
        }
    }
}

/**
 * 
 */
class StateOperator extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            return this.callback(el, iteration);
        }
    }
}

/**
 * 
 */
class StateBuilder extends StateElement {

}


/**
 * 
 */
class StateMachine {
    constructor() {
        this.remIterations = 0;
        this.iteration = 1;
        this.maxIterations = 10;
        this.async = true;

        this.stateElements = new StateMachineCollection(this);
        this.stateOperators = new StateMachineCollection(this);
        this.stateBuilders = new StateMachineCollection(this);
        this.stateRenderers = new StateMachineCollection(this);

        this._postIteration = null;
    }
    get elements() {
        return this.stateElements;
    }
    get operators() {
        return this.stateOperators;
    }
    get builders() {
        return this.stateBuilders;
    }
    get renderers() {
        return this.stateRenderers;
    }
    get postIteration() {
        return this._postIteration;
    }
    set postIteration(x) {
        this._postIteration = x;
    }
    get async() {
        return this._async;
    }
    set async(x) {
        this._async = x;
    }
    reset() {
        this.elements.clear();
        this.iteration = this.remIterations = 0;
        this.builders.elements.forEach((stateBu) => {
            stateBu.execute(1);
        })
    }
    run(iterations) {
        this.builders.elements.forEach((stateBu) => {
            stateBu.execute(1);
        });
        this.advance(iterations);
    }
    advance(iterations) {
        var self = this;
        var _advance = () => {
            self.remIterations = iterations;
            if (self.remIterations === 0 || self.iteration >= self.maxIterations) {
                return;
            }
            var newElements = [];

            var operateOnElement = (stateEl) => {
                var els = [];
                self.operators.elements.forEach((stateOp) => {
                    var r = stateOp.execute(self.iteration, stateEl);
                    if(r) els = els.concat(r);
                });
                return els;              
            };
            var renderElement = (stateEl, async) => {
                var _re = () => {
                    self.renderers.elements.forEach((stateRe) => {
                        stateRe.execute(self.iteration, stateEl);
                    });
                };
                return self.async ? setTimeout(_re,0) : _re();
            }
            self.elements.elements.forEach((stateEl) => {
                var els = operateOnElement(stateEl);
                renderElement(stateEl);
                // setTimeout(()=>{
                    els.forEach((ell)=>{
                        renderElement(ell);
                        newElements.push(ell);
                    });
                    stateEl.state.parent = stateEl;
                // },0);
            });
            var stopIter = false;
            if(self.postIteration) {
                stopIter = self.postIteration(self, newElements, self.iteration);
            }
            self.iteration++;
            if(self.remIterations !== -1) {
                if(self.remIterations !== 0 && !stopIter) {
                    self.remIterations--;
                    setTimeout(_advance, 0);
                }
            } else {
                setTimeout(_advance, 0);
            }
        };
        setTimeout(_advance, 0);
    }
}


export { StateElement, StateMachineCollection, StateMachine, EntityElement, StateOperator, StateRenderer, StateBuilder };